    font-weight: 500;
    opacity: 1;
    right: 0;
    top: 0;
    color: var(--white-color);
    background-color: var(--theme-color);
    z-index: 1;
    height: 44px;
    width: 44px;
    line-height: 44px;
    text-align: center;
    border-radius: 0 15px 0 4px;
  }
  .blog-single.format-quote blockquote, .blog-single.format-quote .wp-block-quote {
